jo de los agroecosistemas bajo estrategias como el uso responsable de los recursos naturales y la conservación de la biodiversidad.")]),_c('hr')])]),_c('div',{staticClass:"row justify-content-center align-items-center mb-3",attrs:{"data-aos":"fade-right"}},[_c('div',{staticClass:"col-auto"},[_c('img',{staticClass:"img-a",attrs:{"src":require("@/assets/curso/temas/tema_1/6.svg"),"alt":""}})]),_c('div',{staticClass:"col-9"},[_c('h5',[_vm._v("Agroecosistemas")]),_c('p',{staticClass:"pe-5 me-5"},[_vm._v("Por medio de la producción a largo plazo donde la velocidad de degradación debe ser inferior o en su defecto igual a la tasa de reposición, es decir se debe hacer de forma eficiente y rentable; también a través de la reducción del impacto del suelo y conservación de su fertilidad mediante prácticas como la rotación de cultivos, la minimización de la contaminación ambiental al no emplear agroquímicos, la producción de alimentos más naturales y de mejor calidad nutritiva y el aporte al compromiso para con las generaciones futuras de alcanzar un desarrollo económico sostenible, donde exista seguridad alimentaria y se asegure la conservación y protección del medio ambiente. ")])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"fondo-2 p-4 mb-5"},[_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-lg-8 py-5",attrs:{"data-aos":"fade-down-right"}},[_c('p',[_vm._v("La agroecología cuenta con diversos enfoques, lo que conlleva a la diferenciación de las ciencias agrícolas tradicionales, porque al surgir distintos orígenes filosóficos y al tener diferentes raíces, hay gran variedad de técnicas, metodologías, consecuencias sociales y ambientales, que difieren trascendentalmente de la agricultura convencional, logrando construir y comprender una nueva forma de implementación de prácticas agrícolas sustentables a través de la recuperación del conocimiento y saberes tradiciones y la aplicación de metodologías participativas de investigación social.")]),_c('p',[_vm._v("Las técnicas agrícolas que se usen, añadirán valor y establecerán las condiciones y reglas del mercado; además, logran un equilibrio ecológico, político, social, económico y de los servicios ecosistémicos.")])]),_c('div',{staticClass:"col-lg-4",attrs:{"data-aos":"fade-down-left"}},[_c('img',{staticClass:"img-a",attrs:{"src":require("@/assets/curso/temas/tema_1/img_9.png"),"alt":""}})])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"bloque-texto-g__texto p-4"},[_c('p',{staticClass:"mb-0"},[_vm._v("La agroecología, por medio de los diversos enfoques y, adicionalmente, a través de cinco ejes temáticos, logra aportar, de forma directa, a los objetivos del desarrollo sostenible, establecidos por la ONU en la agenda 2030; por tanto, es una herramienta que juega un papel fundamental para alcanzarlos, debido a que los enfoques agroecológicos están direccionados para afrontar los orígenes y causales de problemáticas como la pobreza, el hambre y la desigualdad.")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-5 justify-content-center"},[_c('div',{staticClass:"col-lg-10"},[_c('div',{staticClass:"bloque-texto-f color-secundario pt-4 px-5 mb-5"},[_c('div',{staticClass:"bloque-texto-f__comillas"},[_c('i',{staticClass:"fas fa-quote-left"}),_c('i',{staticClass:"fas fa-quote-right"})]),_c('h4',{staticClass:"text-regular bloque-texto-f__texto text-center"},[_vm._v("Se plantearon 17 objetivos de desarrollo sostenible; se caracterizan por ser universales, transformadores y civilizatorios. Están enfocados en lograr un porvenir sostenible, para lo cual se contemplan e incluyen acciones para combatir problemáticas globales que sufre la humanidad diariamente, como son: la pobreza, la desigualdad, la degradación ambiental, la violencia y las injusticias, entre otras.")])]),_c('div',{staticClass:"titulo-sexto color-secundario",attrs:{"data-aos":"fade-up"}},[_c('p',{staticClass:"mb-0"},[_c('b',[_vm._v("Figura 1")]),_c('br'),_vm._v(" "),_c('em',[_vm._v("Objetivos de desarrollo sostenible.")])])]),_c('img',{staticClass:"img-a mb-2",attrs:{"src":require("@/assets/curso/temas/tema_1/img_20.svg"),"alt":"","data-aos":"fade-up"}}),_c('figcaption',{attrs:{"data-aos":"fade-up"}},[_c('p',{staticClass:"mb-0"},[_vm._v("Nota: Tomado de Organización de Naciones Unidas. (s.f.).")])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"fondo-3 p-5"},[_c('div',{staticClass:"row justify-content-center mb-5"},[_c('div',{staticClass:"col-lg-4 mb-4 mb-md-0",attrs:{"data-aos":"fade-left"}},[_c('img',{staticClass:"img-a",attrs:{"src":require("@/assets/curso/temas/tema_1/img_22.png"),"alt":""}})]),_c('div',{staticClass:"col-lg-8",attrs:{"data-aos":"fade-right"}},[_c('div',{staticClass:"bloque-texto-b color-secundario p-4"},[_c('div',{staticClass:"bloque-texto-b__texto"},[_c('p',{staticClass:"mb-0"},[_vm._v("De forma específica, los cinco ejes temáticos de la agroecología contribuyen a diversos objetivos de desarrollo sostenible, al impulsar acciones encaminadas a la conservación y preservación de la biodiversidad, los recursos naturales (el agua, el suelo), la aplicación de metodologías ecológicas y la mitigación de impactos ambientales, el fomento de proceso cortos de comercialización, el aumento de mercados campesinos y de productos orgánicos lo que favorece la seguridad alimentaria, la inclusión del enfoque diferencial, de género, la importancia de la mujer, la generación de valor agregado de los sistemas productivos, recuperación de prácticas y saberes ancestrales, entre otros.")])])])])]),_c('div',{staticClass:"titulo-sexto color-secundario",attrs:{"data-aos":"fade-up"}},[_c('p',{staticClass:"mb-0"},[_c('b',[_vm._v("Figura 2")]),_c('br'),_vm._v(" "),_c('em',[_vm._v("Ejes de la agroecología y relación con los ODS.")])])]),_c('img',{staticClass:"img-a mb-2",attrs:{"src":require("@/assets/curso/temas/tema_1/img_23.svg"),"alt":"","data-aos":"fade-up"}})])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"fondo-4 p-4"},[_c('img',{staticClass:"img-a mb-2",attrs:{"src":require("@/assets/curso/temas/tema_1/img_26.svg"),"alt":"","data-aos":"fade-up"}})])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"tarjeta-slide__contenido p-4 p-xl-4"},[_c('h2',[_vm._v("Mapa del suelo")]),_c('p',[_vm._v("Nota. La imagen muestra los suelos del territorio colombiano. Servicio Geológico Colombiano (2020). ")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"tarjeta-slide__contenido p-4 p-xl-4"},[_c('h2',[_vm._v("Sistemas de Información Geográfica (SIG)")]),_c('p',[_vm._v(" En los procesos de transición, la cartografía es una herramienta importante porque permite obtener una caracterización del territorio, con la finalidad identificar el potencial agrícola y productivo del suelo, mediante una evaluación cuantitativa y cualitativa por medio de una superposición de variables y el análisis de los servicios ecosistémicos, de las propiedades y componentes físicos (inclinación, temperatura, precipitación), socioeconómicos (avalúos del territorio, clasificación de predios por área) y legales, para luego tomar decisiones que permitan brindar un manejo sostenible a este recurso. "),_c('br'),_c('br'),_vm._v("Nota. Mapa de conflictos de uso del suelo. Hernández, A. Rojas, R. Sánchez, F. (2013). ")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"tarjeta-slide__contenido p-4 p-xl-4"},[_c('h2',[_vm._v("Cartografía de usos del suelo")]),_c('p',[_vm._v("A la hora de realizar el proceso de cartografía del suelo, existen diversas metodologías y procedimientos, siendo la zonificación agroecológica un instrumento de gran importancia porque como lo establece Alarcón Trujillo, V. F. (2021), esta permite establecer el potencial del terreno o zona geográfica, lo que a su vez permite un óptimo aprovechamiento del suelo. La forma de realizar la zonificación incluye actividades que pueden ser clasificadas en 3 grupos, que son: registro del uso de las tierras, la compilación de los registros e inventarios de tierras y la evaluación de aptitudes.")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"bloque-texto-g__texto p-4"},[_c('p',{staticClass:"mb-0"},[_vm._v("La agroecología por medio de la caracterización del territorio, logra identificar todas las problemáticas que se presentan, con el fin de minimizarlas porque de acuerdo a la vulnerabilidad de los ecosistemas y el conflicto de los procesos geofísicos con la población, se convierten en riesgos, los cuales pueden ser de diversos orígenes, entre los que se destacan: afectaciones del clima y de las condiciones meteorológicas, desastres naturales, daños físicos, locativos, afectaciones económicas y financieras derivadas de fluctuaciones en los precios y procesos de mercadeo.")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-5 mt-5"},[_c('div',{staticClass:"col-lg-4 mb-4 mb-md-0",attrs:{"data-aos":"fade-right"}},[_c('img',{staticClass:"img-a mb-2",attrs:{"src":require("@/assets/curso/temas/tema_1/img_31.png"),"alt":"","data-aos":"fade-up"}})]),_c('div',{staticClass:"col-lg-8",attrs:{"data-aos":"fade-left"}},[_c('p',{staticClass:"mb-3"},[_vm._v("Existen medidas para reducir y controlar los riesgos por medio de la minimización de la pérdida de activos físicos y capital ambiental, como también por medio de la implementación de instrumentos de planificación, como la zonificación ambiental, el ordenamiento territorial y los adecuados usos del suelo. Además, el proceso de análisis de los riesgos lo conforman tres fases que van desde el análisis de los factores de riesgo, para luego realizar una evaluación y por último, diseñar medidas de mitigación. ")]),_c('div',{staticClass:"cajon color-primario p-4 mb-4 align-items-center"},[_c('p',{staticClass:"mb-0"},[_vm._v("Por tanto, el proceso de gestión de riesgos empieza con la identificación de las amenazas y vulnerabilidades, las cuales ponen en riesgo la funcionalidad y estabilidad de las unidades funcionales del territorio; debido a que son una serie de eventos de origen natural vinculados con fenómenos atmosféricos, hidrológicos, geológicos, volcánicos, sísmicos, como por ejemplo huracanes, tornados, inundaciones, tsunamis, avalanchas, hundimientos, erupciones, sequías, lluvias o vientos intensos, temblores, terremotos, incendios, etc...")])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-lg-8 mb-4 mb-md-0",attrs:{"data-aos":"fade-left"}},[_c('p',{staticClass:"mb-3"},[_vm._v("Como también pueden ser de origen antrópico, resultado de la sobreexplotación y/o degradación del medio ambiente y en general de la inapropiada relación con el entorno, lo que ocasiona deforestación, desertificación, deslizamientos, salinización de suelos, pérdida de biodiversidad, etc.")]),_c('div',{staticClass:"cajon color-primario p-4 mb-4"},[_c('p',{staticClass:"mb-0"},[_vm._v("Luego de realizar el proceso adecuado de identificación, se requiere establecer el impacto generado por el evento en términos de área afectada, intensidad o número de víctimas, es decir, la severidad, como también la frecuencia o número de veces que ocurre el evento en un determinado período de tiempo. Para con ello, lograr establecer acciones de carácter preventivo y correctivo que permitan no solo reducir la vulnerabilidad, sino que también anticiparse a su probabilidad de ocurrencia y a sus posibles consecuencias.")])])]),_c('div',{staticClass:"col-lg-4",attrs:{"data-aos":"fade-right"}},[_c('img',{staticClass:"img-a mb-2",attrs:{"src":require("@/assets/curso/temas/tema_1/img_32.png"),"alt":"","data-aos":"fade-up"}})])])}]

